.form-card {
  background-color: $white;
  border-radius: $base-gutter * 1.5;
  box-sizing: border-box;
  box-shadow: 0px 1px 4px #E3EBFC, 0px 24px 48px rgba(230, 235, 245, 0.4);
  display: flex;
  flex-direction: column;
  padding: $base-gutter * 4;
  row-gap: $base-gutter * 4 ;
  width: $base-gutter * 88;
  &__header {
    align-items: center;
    border-bottom: 1px solid $bg-disable;
    color: $gosblue;
    display: flex;
    justify-content: space-between;
    padding-bottom: $base-gutter * 4;
  }
  &__footer {
    border-top: 1px solid $bg-disable;
    column-gap: $base-gutter * 2;
    display: flex;
    padding-top: $base-gutter * 4;
  }
  &__list {
    display: flex;
    flex-direction: column;
    row-gap: $base-gutter * 5;
  }
  &__item {
    display: flex;
    flex-direction: column;
    row-gap: $base-gutter * 4;
    &-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
      margin: 0;
    }
    &-inner {
      display: flex;
      flex-direction: column;
      row-gap: $base-gutter * 2;
      &.is-uploader {
        row-gap: $base-gutter * 3;
        margin-top: -$base-gutter;
      }
    }
  }
  &__row {
    column-gap: $base-gutter * 3;
    display: grid;
    grid-template-columns: 1fr 2fr;
    &:nth-child(even) {
      grid-template-columns: 1fr;
    }
    .is-uploader & {
      grid-template-columns: 1fr;
    }
    &-title {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      margin: 0;
      margin-bottom: $base-gutter * 1.5;
    }
  }
  &__title {
    color: $summer-night;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    margin: 0;
  }
}
