.logo {
    display: flex;
    &__visually-hidden {
        border: 0;
        clip-path: inset(100%);
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        position: absolute;
        padding: 0;
        width: 1px;
        white-space: nowrap;
    }
}
