.page-header {
    align-items: center;
    background-color: $white;
    box-shadow: 0px 2px 2px rgba(157, 172, 207, 0.08);
    display: flex;
    font-family: 'Lato', 'Arial', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    min-height: $base-gutter * 8;
    &__navigation {
        align-items: center;
        column-gap: $base-gutter * 3;
        display: grid;
        flex-grow: 1;
        grid-template-columns: 1fr auto auto auto;
        max-width: $base-gutter * 152;
        margin: 0 auto;
    }
    &__site-navigation {
      column-gap: $base-gutter * 3;
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        margin: 0;
        padding: 0;
        &-link {
            color: $gosblue;
            display: block;
            text-decoration: none;
        }
    }
    &__icon-navigation {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        margin: 0;
        margin-left: auto;
        padding: 0;
        &-item {
            margin-right: $base-gutter;
            &:last-child {
                margin-right: 0;
            }
        }
        &-link {
            color: $gosblue;
            display: block;
            text-decoration: none;
        }
    }
    &__visually-hidden {
        border: 0;
        clip-path: inset(100%);
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        position: absolute;
        padding: 0;
        width: 1px;
        white-space: nowrap;
    }
}

