.user-navigation {
  color: #0D4CD3;
  column-gap: $base-gutter * 0.5;
  align-items: center;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
  &__icon {
    align-items: center;
    column-gap: $base-gutter * 0.5;
    display: flex;
    &-text {
      align-items: center;
      border-radius: 100%;
      background-color: $petalo-rosa;
      color: #fff;
      color: rgba($blue-traffico, 0.6);
      display: flex;
      font-size: 14px;
      font-weight: 700;
      height: 36px;
      justify-content: center;
      line-height: 20px;
      overflow: hidden;
      width: 36px;
    }
    &-dropdown {
      transform: rotate(180deg);
      .is-toggle & {
        transform: rotate(0deg);
      }
    }
  }
  &-link {
      cursor: pointer;
      box-sizing: border-box;
      border-radius: $base-gutter;
      background-color: $tagline;
      color: $gosblue;
      display: block;
      padding: $base-gutter * 0.75 $base-gutter * 2;
      text-decoration: none;
  }
  &__menu {
      background-color: $white;
      box-sizing: border-box;
      box-shadow: 0px 1px 4px #E3EBFC, 0px 24px 48px rgba(230, 235, 245, 0.4);
      border-radius: $base-gutter * 1.5;
      display: none;
      flex-direction: column;
      row-gap: $base-gutter * 2;
      position: absolute;
      padding: $base-gutter * 3 0;
      right: 0;
      top: $base-gutter * 8;
      z-index: 10;
      width: $base-gutter * 43;
      .is-toggle & {
        display: flex;
      }
  }
  &__header {
      margin: 0 $base-gutter * 3;
      padding-bottom: $base-gutter *3;
      border-bottom: 1px solid $pervinca;
  }
  &__title {
      color: $summer-night;
      column-gap: $base-gutter * 2;
      display: flex;
      &__text {
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          margin: 0;
      }
      & > *:first-child {
          cursor: default;
          flex-shrink: 0;
          height: $base-gutter * 6;
          width: $base-gutter * 6;
      }
  }
  &__organization {
      color: $text-helper;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      margin: 0;
      margin-top: $base-gutter * 2;
  }
  &__info {
      align-items: flex-start;
      display: inline-flex;
      flex-direction: column;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      margin: 0 $base-gutter * 3;
      padding-bottom: $base-gutter *3;
      border-bottom: 1px solid $pervinca;
      row-gap: $base-gutter * 2;
      &-item {
          color: $summer-night;
          margin: 0;
          &:hover {
              color: $summer-night;
          }
      }
  }
  &__list {
      display: flex;
      flex-direction: column;
      row-gap: $base-gutter;
  }
  &__item {
      cursor: pointer;
      column-gap: $base-gutter;
      display: flex;
      padding: $base-gutter $base-gutter * 3;
      &__text {
          margin: 0;
      }
      &:hover {
          background-color: #FEF0CC;
      }
      // &:not(:last-child) {
      //     display: none; // Временно скрыл
      // }
  }
  &__avatar {
    align-items: center;
    border-radius: 100%;
    background-color: $petalo-rosa;
    cursor: pointer;
    color: rgba($blue-traffico, 0.6);
    display: flex;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    height: $base-gutter * 4.5;
    justify-content: center;
    overflow: hidden;
    width: $base-gutter * 4.5;
  }
}
