.catalog {
  &__container {
    margin: 0 auto;
    max-width: $base-gutter * 152;
    padding-top: $base-gutter * 12;
  }
  &__headline {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: $base-gutter * 8;
    &-text {
      font-family: 'Lato', 'Arial', sans-serif;
      font-weight: 900;
      font-size: 40px;
      line-height: 48px;
      margin: 0;
    }
  }
  &__banner {
    border: 2px dashed  $bg-disable;
    background-image: url("../../../img/icon__banner.svg");
    background-position: center top;
    background-repeat: no-repeat;
    border-radius: $base-gutter * 1.5;
    display: flex;
    justify-content: center;
    height: $base-gutter * 34;
    margin-bottom: $base-gutter * 8;
    width: 100%;
    &-text {
      align-self: flex-end;
      color: $summer-night;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      margin: 0;
      margin-bottom: $base-gutter * 5;
    }
  }
}
