img {
  max-width: 100%;
  height: auto;
}
html {
  height: 100%;
}

body {
  min-width: 320px;
  background-color: $background;
  color: $summer-night;
  display: grid;
  font-family: 'Lato', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  height: 100%;
}
