.filter {
  display: flex;
  flex-direction: column;
  margin-left: -$base-gutter * 3;
  margin-right: -$base-gutter * 3;
  row-gap: $base-gutter * 2;
  &__card {
    align-items: end;
    box-shadow: 0px 1px 4px #E3EBFC, 0px 24px 48px rgba(230, 235, 245, 0.4);
    background-color: $white;
    border-radius: $base-gutter * 1.5;
    box-sizing: border-box;
    column-gap: $base-gutter * 11;
    display: grid;
    grid-template-columns: 1fr auto;
    padding: $base-gutter * 3;
  }
  &__item {
    column-gap: $base-gutter * 4;
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
  }
  &__tags {
    border-top: 1px solid $pervinca;
    column-gap: $base-gutter * 2;
    display: flex;
    flex-wrap: wrap;
    margin-left: $base-gutter * 3;
    margin-right: $base-gutter * 3;
    padding: $base-gutter * 2 0;
    row-gap: $base-gutter;
  }
}
